@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
:root {
  --primary-white: #fff;
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
}

body {
  font-family: "Roboto Condensed" sans-serif;
}
/* Navbar */

.logo {
  width: 3.25rem;
}

.navbar {
  position: -webkit-sticky;
  position: sticky;
}

/* Menu links */
nav a.nav-link {
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #fff !important;
  color: var(--primary-white) !important;
}

.navbar-brand {
  max-width: 17.25rem;
  padding: 0 i !important;
  /* height: 6rem !important; */
}

/* .navbar-nav > li > a.active, .navbar-nav > li > a.active:focus { */
.navbar-nav > li > a.active,
.navbar-nav > li > a:focus {
  /* border-top: .1875rem solid var(--primary-white); */
  border-bottom: 0.1875rem solid #fff;
  border-bottom: 0.1875rem solid var(--primary-white);
}

.navbar-nav > li > a:hover {
  /* border-top: .1875rem solid var(--primary-white); */
  border-top: 0.1875rem solid #fff;
  border-top: 0.1875rem solid var(--primary-white);
}
.btn:hover {
  background-color: rgba(255, 255, 255, 0.342);
}

/* Header */

.header-wrapper {
  position: relative;
  background: url(/JLaroPortfolio/static/media/HeaderLogo.ed286a3a.png) no-repeat;
  background-size: cover;
  background-position: center;
  height: 90vh;
}

.main-info {
  display: -webkit-flex;
  display: flex;
  position: absolute;
  top: 25rem;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  font-family: "Roboto Condensed", sans-serif;
}

.main-info h1 {
  text-transform: uppercase;
}
@media (max-width: 768px) {
  .main-info h1 {
    font-size: 1.5rem !important;
  }
}

/* Header main offer */
.btn-main-offer {
  border: 1px solid #ef4035;
  border: 1px solid var(--primary-red);
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
}

.about-wrapper {
  position: relative;
  /* background: url(./images/HeaderLogo.png) no-repeat; */
  background-color: #092a47;
  background-size: cover;
  background-position: center;
  height: 90vh;
}

